<template>
    <div>
        <div>
            <span class="sswz">搜索：</span>
            <input v-model="sousou" placeholder="圣诞节" @change="slgedan" style="width: 60%;margin-top: 20px;margin-bottom: 10px;height: 20px">
        </div>
        <ul class="gedan">
            <li v-for="song in ret" :key="song.id" class="gelan">
                <div class="topimg">
                    <img :src="song.coverImgUrl" alt="">
                    <div class="el-icon-video-play but" @click="bofang(song)">
                    </div>
                </div>
                <div class="imgname">
                    <p>{{song.name}}</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: "gedan",
        data(){
          return{
              sousou:"",
              ret:[]
          }
        },
        //添加默认
        created(){
            this.$axios.get("https://api.imjad.cn/cloudmusic/", {
                params: {
                    type: "search",
                    s: "圣诞节",
                    search_type: 1000
                }
            }).then(res => {
                console.log(res)
                if (!res.data.result) {
                    alert("搜索结果为空！")
                } else {
                    this.ret = res.data.result.playlists
                    // console.log(this.ret)
                }
            }).catch(err => {
                console.log(err)
            })
        },
        methods: {
            slgedan() {
                this.$axios.get("https://api.imjad.cn/cloudmusic/", {
                    params: {
                        type: "search",
                        s: this.sousou,
                        search_type: 1000
                    }
                }).then(res => {
                    // console.log(res)
                    if (!res.data.result) {
                        alert("搜索结果为空！")
                    } else {
                        this.ret = res.data.result.playlists
                        // console.log(this.ret)
                    }
                }).catch(err => {
                    console.log(err)
                })
            },
            bofang(song){
                this.$axios.get("https://api.imjad.cn/cloudmusic/",{
                    params:{
                        type: "playlist",
                        id: song.id,
                        search_type: 1000
                    }
                }).then(res=>{
                    console.log(res)
                    this.$router.push({
                        name:"gedanlist",
                        params:{
                            songlist: res.data.playlist.tracks,
                            listall: res.data.playlist
                        }
                    })

                })
            }
        }
    }
</script>

<style scoped>
    .gedan{
        width: 100%;
        margin: 10px auto;
        padding: 0;
        list-style: none;
        background: white;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
    }
    .sswz{
        color: white;
        padding-right: 20px;
    }
    .gelan{
        width: 150px;
        height: 180px;
        line-height: 50px;
        text-align: left;
        padding: 10px;
    }
    .topimg{
        height: 150px;
        width: 150px;
    }
    .topimg>img{
        width: 150px;
    }
    .imgname{
        height: 30px;
        width: 150px;
        line-height: 30px;
        text-align: center;
        align-items: center;
    }
    .imgname>p{
        line-height: 20px;
        margin: 0;
        font-size: 12px;
    }
    .but{
        position: relative;
        top: -60px;
        right: -115px;
        font-size: 30px;
        color: white;
        cursor: pointer;
    }
    .but:hover{
        color: red;
    }
</style>
